import React, { Component } from 'react'
import Validation from "./validation"   // import Validation component for display validation status

export default class SelectBox extends Component {
    render() {

        let currentAnswer = this.props.currentAnswer;

        let selectbox_options = this.props.question_object.Options.map((current_option) => {
            let currentOptionId = current_option.option_id;
            return(
                <option key={currentOptionId} id={currentOptionId} value={current_option.option} 
                selected={currentAnswer[currentOptionId]}>{current_option.option}</option>
            );
        });

        let multiple_dropdown = this.props.question_object.Options.map((current_option) => {
            return(
                <div className="col-6 my-2">
                    <select className="form-control" onChange={this.props.selectAnswer}>
                        {selectbox_options}
                    </select>
                </div>
            );
        });

        return (
            <React.Fragment>
                <h4 key={this.props.question_object.question_id}>
                    <span className="mx-1">Q.{this.props.active_index + 1}</span>
                    {this.props.question_object.Question}
                </h4>
                
                <div className="row my-4">
                    {multiple_dropdown}                
                </div>
                <Validation status={this.props.validationWarning} type="select" />
            </React.Fragment>
        )
    }
}
