import React, { Component } from 'react'
import "./kbc.css"
import question_bank from "./question_bank"     // array of question's data
import Text from "./input_type_text"            // import Text component
import Radio from './input_type_radio'          // import Radio component
import Range from "./input_type_range"          // import Range component
import SelectBox from './input_type_select'     // import SelectBox component
import Checkbox from "./input_type_checkbox"    // import Checkbox component
import Button from "./button"                   // import Button component
import Finish from './finish'                   // import Finish component

export default class Questions extends Component {

    userAnswers = [];               // stores all question's answer
    activeAnswer = {};              // stores current active individual question's answer temporary          

    state = {
        currentQuestion: 0,         // current question object's index of question_bank array
        submited: false,            // still not in use        
        activeQuestionAnswer: {},   // stores current active individual question's answer in object temporary
        validationWarning: "valid",
        finished: false,
        showFinishMessage: false
    }

    previousQuestion = () => { // for rander next question 

        this.setState({finished: false});

        this.setState({validationWarning: "valid"});
        // console.log(this.state.validationWarning);

        if(this.state.currentQuestion > 0) {
            this.setState({
                currentQuestion: this.state.currentQuestion - 1          
            });
        }
    }

    nextQuestion = () => {  // for rander next question and also cleare activeAnswer & state.activeQuestionAnswer
            
        if(Object.keys(this.activeAnswer).length === 0) {   // validation for answer
            if(!this.userAnswers[this.state.currentQuestion]) {
                this.setState({validationWarning: "invalid"});
                return false;
            }
        } 

        if(this.userAnswers[this.state.currentQuestion]) {
            if(this.state.currentQuestion > -1) {
                this.userAnswers.splice(this.state.currentQuestion, 1);
            }
            this.userAnswers.splice(this.state.currentQuestion, 0, this.state.activeQuestionAnswer);
        }

        this.activeAnswer = {};
        this.userAnswers.push(this.state.activeQuestionAnswer);
        this.setState({activeQuestionAnswer: {}});
        console.log(this.userAnswers);

        if(this.state.currentQuestion === (question_bank.length - 2)) {
            this.setState({finished: true});
        }

        if(this.state.currentQuestion < (question_bank.length - 1) ) {
            this.setState({currentQuestion: this.state.currentQuestion + 1});
        }
    }

    textAnswer = (e) => {   // for store answer of input type text

        console.log("hello World");

        this.setState({validationWarning: "valid"});

        let id= e.target.id.toString();
        let answer = e.target.value;

        if(answer.trim() !== "") {
            this.activeAnswer["q_id"] = question_bank[this.state.currentQuestion].question_id;
            this.activeAnswer[id] = answer.trim();

            this.setState({activeQuestionAnswer: this.activeAnswer});
        }
    }

    selectAnswer = (e) => {  // for store answer of input type select

        this.setState({validationWarning: "valid"});

        this.activeAnswer = {};
        this.setState({activeQuestionAnswer: {}});

        let option_id = e.target.options[e.target.selectedIndex].id.toString();

        this.activeAnswer["q_id"] = question_bank[this.state.currentQuestion].question_id;
        this.activeAnswer[option_id] = true;

        this.setState({activeQuestionAnswer: this.activeAnswer});
    }

    radioAnswer = (e) => {  // for store answer of input type radio

        this.setState({validationWarning: "valid"});

        this.activeAnswer = {};
        this.setState({activeQuestionAnswer: {}});

        let option_id = e.target.id.toString();

        this.activeAnswer["q_id"] = question_bank[this.state.currentQuestion].question_id;
        this.activeAnswer[option_id] = true;

        this.setState({activeQuestionAnswer: this.activeAnswer});
    }

    rangeAnswer = (e) => {
        //debugger;
        // console.log("Hello World : ", e.target.value)

        this.setState({validationWarning: "valid"});

        this.activeAnswer = {};
        this.setState({activeQuestionAnswer: {}});
        
        let option_id = e.target.id.toString();
        let rangeValue = e.target.value;

        this.activeAnswer["q_id"] = question_bank[this.state.currentQuestion].question_id;
        this.activeAnswer[option_id] = rangeValue;

        this.setState({activeQuestionAnswer: this.activeAnswer});
    }

    componentDidUpdate(prevProps, prevState) {
        // console.log("componentDidUpdate invoked");
        // console.log("Already given Answer : ", this.userAnswers);
        console.log(prevState);

        if(prevState.currentQuestion !== this.state.currentQuestion && this.userAnswers[this.state.currentQuestion]) {
            let activeQuestionAnswer = this.userAnswers[this.state.currentQuestion];
            this.setState({activeQuestionAnswer: activeQuestionAnswer});
        }
    }

    render() {

        let Question_Type="";
        let finishButton = <Button button_usage="Finish" operation={() => {
            if(this.state.validationWarning !== "valid") {
                alert("Please fill answer");
            } else {
                this.setState({showFinishMessage: true});                
            }
            
        }} />

        const showFinishedButton = () => {
            if(this.state.finished) {
                return finishButton;
            } else return null
        }

        switch(question_bank[this.state.currentQuestion].type) {
            case "text":
                Question_Type = <Text active_index={this.state.currentQuestion} textAnswer={this.textAnswer} currentAnswer={this.state.activeQuestionAnswer}
                question_object = {question_bank[this.state.currentQuestion]} validationWarning={this.state.validationWarning} />
                break;

            case "radio":
                Question_Type = <Radio active_index={this.state.currentQuestion} radioAnswer={this.radioAnswer} currentAnswer={this.state.activeQuestionAnswer}
                question_object = {question_bank[this.state.currentQuestion]} validationWarning={this.state.validationWarning} />
                break;

            case "checkbox":
                Question_Type = <Checkbox active_index={this.state.currentQuestion} radioAnswer={this.radioAnswer} currentAnswer={this.state.activeQuestionAnswer}
                question_object = {question_bank[this.state.currentQuestion]} validationWarning={this.state.validationWarning} />
                break;

            case "range":
                Question_Type = <Range active_index={this.state.currentQuestion} rangeAnswer={this.rangeAnswer} currentAnswer={this.state.activeQuestionAnswer}
                question_object = {question_bank[this.state.currentQuestion]} validationWarning={this.state.validationWarning} />
                break;

            case "select":
                Question_Type = <SelectBox active_index={this.state.currentQuestion} selectAnswer={this.selectAnswer} currentAnswer={this.state.activeQuestionAnswer}
                question_object = {question_bank[this.state.currentQuestion]} validationWarning={this.state.validationWarning}/>
                break;
            
            default:
                Question_Type = <Radio active_index={this.state.currentQuestion} currentAnswer={this.state.activeQuestionAnswer}
                question_object = {question_bank[this.state.currentQuestion]} validationWarning={this.validationWarning} />
        }

            if(this.state.showFinishMessage) {
                return(
                    <Finish />
                )
            }

            return (
                <React.Fragment>
                    <div className="row justify-content-center my-4">
                        <div className="col-1 py-5">
                            <Button operation={this.previousQuestion} button_usage="Previous" />
                        </div>
                        <div className="col-6 border border-dark my-2">
                            
                            {Question_Type}
                            
                        </div>
                        <div className="col-1 py-5">
                            <Button operation={this.nextQuestion} button_usage="Next" />  
                            {
                                showFinishedButton()
                            }
                        </div>
                    </div>
                
                </React.Fragment>
            )
    }
}
