import React, { Component } from 'react'
import Validation from "./validation"   // import Validation component for display validation status

export default class Checkbox extends Component {
    
    render() {

        let currentAnswer = this.props.currentAnswer;

        let options = this.props.question_object.Options.map((current_option) => {

                let currentOptionId = current_option.option_id;
                return (
                    <div className="col-6" key={currentOptionId}>
                        <input type="checkbox" name={current_option.option} id={currentOptionId} key={this.props.question_object.Options.option_id}
                         value={current_option.option} onChange={this.props.radioAnswer}
                          checked={currentAnswer[currentOptionId]}  />
                        <label htmlFor={currentOptionId}  className="font-weight-bold mx-1">{current_option.option}</label>
                    </div>
                );
            }); 

        return (
            <React.Fragment>
                <h4 key={this.props.question_object.question_id}>
                    <span className="mx-1">Q.{this.props.active_index + 1}</span>
                    {this.props.question_object.Question}
                </h4>
                <div className="row my-4">
                   {options}
                </div>
                <Validation status={this.props.validationWarning} type="radio" />
            </React.Fragment>
        )
    }
}
