import React, { Component } from 'react'
import Validation from "./validation"   // import Validation component for display validation status

export default class Text extends Component {
    render() {
        let currentAnswer = this.props.currentAnswer;

        let options = this.props.question_object.Options.map((current_option) => {
            // console.log(current_option.option_id);

            let currentOptionId = current_option.option_id;
            
            return(
                <div className="col-6" key={current_option.option_id}>
                    <input type="text" name="kbc_option" value={currentAnswer[currentOptionId]} id={currentOptionId}  className="form-control my-2" onChange={this.props.textAnswer} />
                </div>
            );
        });

        //console.log("text-options_objext", this.props.question_object.Options);
        
        return (
            <React.Fragment>
                <h4 key={this.props.question_object.question_id}>
                    <span className="mx-1">Q.{this.props.active_index + 1}</span>
                    {this.props.question_object.Question}
                </h4>
                <div className="row my-4">
                   {options} 
                </div>
                <Validation status={this.props.validationWarning} type="text" />
            </React.Fragment>
        )
    }
}
