import React, { Component } from 'react'

export default class Validation extends Component {
    render() {
        console.log(this.props.status);

        let warning;

        switch(this.props.type) {
            case "radio":
                warning = "Please choose any option";
                break;

            case "select":
                warning = "Please select Answer";
                break;

            case "text":
                warning = "Please Enter Answer";
                break;

            case "range":
                warning = "Please choose answer";
                break;

            default:


        }

        if(this.props.status === "valid") {
            return(
                <div className="mt-4 d-none">
                    <h6 className="text-danger font-weight-bold">{warning}</h6>
                </div>
            ) 
        } else {
            return(
                <div className="mt-4">
                    <h6 className="text-danger font-weight-bold">{warning}</h6>
                </div>
            )   
        }
    }
}
