import React, { Component } from 'react'
import animationColors from "./animation_styles"

export default class Finish extends Component {

    state = {
        colors: animationColors,
        timer: 1
    }

    changeColors = () => {
        if(this.state.colors[0] !== this.state.colors[0]) {
            this.setState({colors: animationColors});
        }
    }

    render() {

        setInterval(
            this.changeColors(),1000
        );

        let msg = ["H", "A", "V", "E", "", "A", "", "G", "O", "O", "D", "", "D", "A", "Y"];
        const showMsg = msg.map((letter) => {
            return(
                <div className="text-center" style={this.state.colors[Math.floor(Math.random() * animationColors.length)]}>
                    <span className="py-3 h4 px-4 font-weight-bold">{letter}</span>
                </div>
            );
        });


        return (
            <div className="row justify-content-center my-5">
                <div className="col-8">
                    <div className="h2 text-center text-success">
                        Participation Completed
                        <span className="text-dark">âœ”</span>
                    </div>
                    <div>
                        <div className="row my-5">
                              {showMsg}
                        </div>
                    </div>
                </div> 
            </div>
        )
    }
}
